*{
  font-family: 'Roboto Condensed',  neue-haas-grotesk-display, dnp-shuei-gothic-kin-std,sans-serif;
  font-weight: 400;
}

h1{
  font-family: adobe-handwriting-frank;
}

h2, h3, h4, h5, h6, dt, span.resume-date, .nav-link{
  font-family: 'Roboto'
 
}

p, dd, li{
  color: #808080;
  text-align: justify;
}

@media only screen and (max-width: 576px) {
  p, dd, li, span{
    font-size: 0.9rem;
    text-align: start;
  }

}
.pace-running .wrap {
  display: none;
}

.enmel{
  /*display: none;*/
}



div.resume-date{
    margin-bottom: 8px;
}

span.resume-date{
    color: whitesmoke;
    background-color: black;
    padding: 2px 15px;
    font-weight: bold;
}


/* Works */
.contents-works{
	margin-bottom: 50px;
}

@media only screen and (max-width: 576px) {
  .contents-works{
    margin-bottom: 25px;
  }
}

/* contacts */
.social-icons a {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #343a40;
  color: #fff !important;
  border-radius: 100%;
  border: 3px solid #343a40;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.15rem;
  margin-right: 0.8rem;
  transition: 0.3s;
}

.social-icons a:last-child {
  margin-right: 0;
}

.social-icons a:hover {
  background-color: #ffffff;
  transition: 0.3s;
}

.social-icons a:hover > .fab, .social-icons a:hover > .far{
    color: #343a40;

}

/* footer */

.footer-copyright{
    font-family: ff-dax-pro, serif;
    font-weight: 300;
    letter-spacing: 1px;
}
